import axios from "axios";
import { toast } from "react-toastify";

/*This interceptor catches errors (in this case only for response)
and analyses if there is expected or unexpected
- if unexpected teh error is logged
*/
axios.interceptors.response.use(null, (error) => {
  const expectedError =
    error.response &&
    error.response.status >= 400 &&
    error.response.status < 500;

  if (!expectedError) {
    console.log("Logging the error", error);
    toast("An unexpected error occurred.");
  }
  return Promise.reject(error);
});

export default {
  get: axios.get,
  post: axios.post,
  put: axios.put,
  delete: axios.delete,
};